.about {
  padding: calc(var(--gap-huge) - var(--gap-min)) 0;
  background-color: var(--bg-primary-color);

  &__title {
    text-transform: uppercase;
    font: var(--font-montserrat-medium-xl);
    font-size: clamp(var(--font-size-l), 3vw, 30px);
    margin-top: 0;
    margin-bottom: var(--gap-medium);
    display: flex;
    align-items: center;
    position: relative;
    margin-left: var(--gap-small);

    &::before {
      content: "";
      background: var(--line-color);
      width: clamp(2px, 0.5vw, 4px);
      position: absolute;
      height: 100%;
      left: calc(var(--gap-small) * -1);
    }
  }

  &__box {
    display: flex;
    align-items: center;
    justify-content:space-evenly;
  }

  &__content {
    margin-left: 100px;
    width: clamp(10%, 70%, 650px);
    margin-right: var(--gap-small);
  }

  &__paragraph {
    line-height: var(--line-height-25);
    letter-spacing: var(--letter-spacing-016);
    font-weight: 300;
    color: var(--text-color-opacity-08);
    white-space: pre-wrap;
  }

  &__contact-me {
    line-height: var(--line-height-25);
    letter-spacing: var(--letter-spacing-016);
    font-weight: 300;
    color: var(--text-color-opacity-08);
  }

  &__contacts {
    display: flex;
    align-items: center;
    width: fit-content;
    text-decoration: none;
    color: var(--details-color);
    padding-top: var(--gap-small);
    font: var(--font-montserrat-medium-m);

    &:hover {
      transform: scale(1.1);
      transition: transform 300ms;
      text-decoration: underline;
    }

    &::before {
      content: url('../../assets/icons/letter-icon.svg');
      width: 16px;
      height: 20px;
      position: relative;
      margin-right: 11px;
    }
  }

  &__img {
    width: clamp(200px, 30%, 435px);
    height: clamp(200px, 40%, 435px);
    border: 1px solid opacity(0);
    border-radius: 50%;
  }
}

@media (max-width: 1100px) {
  .about__content {
    min-width: 330px;
    margin-left: 6vw;
    margin-right: 0;
  }

  .about__img {
    flex-shrink: 0;
    margin-bottom: var(--gap-small);
  }

  .about__box {
    flex-direction: column-reverse;
  }
}

@media (max-width: 620px) {
  .about {
    padding: var(--gap-medium) 0;
  }

  .about__title {
    margin-bottom: var(--gap-small);
    margin-left: var(--gap-min);

    &::before {
      left: calc(var(--gap-min) * -1);
    }
  }

  .about__content {
    width: 330px;
    margin: 0;
  }

  .about__box {
    display: flex;
    align-items: center;
    flex-direction: column-reverse;
    justify-content: center;
  }

  .about__contact-me {
    display: none;
  }

  .about__contacts {
    margin-top: var(--gap-min);
    padding: 0;
    border: none;
    font-size: var(--font-size-s);

    &::before {
      content: url('../../assets/icons/letter-icon.svg');
      width: 16px;
      height: 20px;
      position: relative;
      margin-right: 13px;
    }
  }
}
